import React, { Component } from 'react'
import { Modal, Row, Card, Col } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';


const ColCard = ({ icon, title, onClick }) => <Col sm={6}>
    <Card id={`m-${icon}_btn`} className='card-stretch gutter-b cursor-pointer' onClick={onClick}>
        <Card.Body className='flex-column f-center px-2' style={{ height: 150 }}>
            <span className='h-40px w-40px rounded-2 bg-dark text-light mb-3 f-center fs-4'><FontAwesomeIcon icon={icon} /></span>
            <h5 className='text-center fs-8'>{title}</h5>
        </Card.Body>
    </Card>
</Col>

const ManualUploadModal = ({ show, handleClose, uploadCsv, onAddProduct }) => {
    return <Modal show={show} size="md" onHide={handleClose} id="manual_upload_m" centered>
        <Modal.Header closeButton>
            <Modal.Title>Manual Upload Products</Modal.Title>
        </Modal.Header>
        <Modal.Body></Modal.Body>
        <Modal.Body>
            <div className="syncPro_modal">
                <Row className='px-sm-10'>
                    <ColCard icon="download" title="Upload CSV File" onClick={uploadCsv} />
                    <ColCard icon="bag-shopping-plus" title="Add Single Product" onClick={onAddProduct} />
                </Row>
            </div>
        </Modal.Body>
    </Modal>
}
export default ManualUploadModal