import { Container, Card, Row, Col, Image } from 'react-bootstrap'
import { baseUrl } from '../../../../../Assets/AssetsHelper'
const CMSBrands = [
    {
        title: 'Shopify',
        icon: 'shopify',
        fun: 'shopify'
    },
    {
        title: 'Google Merchant Center',
        icon: 'google',
        fun: 'google'
    },
    {
        title: 'BigCommerce',
        icon: 'bigcommerce',
        fun: 'bmc'
    },
    {
        title: 'Magento',
        icon: 'magento',
        fun: 'magento'
    },
    {
        title: 'Woocommerce',
        icon: 'woocommerce',
        fun: 'woocommerce'
    },
    {
        title: 'Manual Upload',
        icon: 'manual-upload',
        fun: ''
    }
]
export const SyncProducts = ({ openManualSyncModal, openSyncProduct }) => <Container>
    <div className='f-center w-100 sync_products' style={{ minHeight: 'calc(100vh - 100px)' }}>
        <Row className='justify-content-center pb-lg-10'>
            <Col lg={8} className='w-100'>
                <div className="mb-5 mb-lg-10">
                    <h3>Sync Product Catalog</h3>
                    <p className='fw-medium text-muted'>Select the application from which you want to tag products</p>
                </div>
                <Row className='gx-3 w-100'>
                    {CMSBrands?.length ? CMSBrands.map((Item, Index) => <Col xs={6} sm={4}>
                        <Card key={Index} id={`${Item.icon}_btn_`} className='card-stretch gutter-b cursor-pointer' onClick={() => Item.fun == '' ? openManualSyncModal() : openSyncProduct(Item.fun)}>
                            <Card.Body className='flex-column f-center px-2'>
                                <Image className={`my-3 ${Item.fun == 'bmc' || Item.fun == '' ? 'invert' : ''}`} src={baseUrl(`media/images/cms/${Item.icon}.svg`)} height={40} width={40} alt={Item.title} fluid/>
                                <h5 className='text-center fs-8' style={{ minHeight: 30 }}>{Item.title}</h5>
                            </Card.Body>
                        </Card>
                    </Col>) : null}
                </Row>
            </Col>
        </Row>
    </div>
</Container>