import React, { Component } from 'react'
import { Container, Card, Table, Tooltip, OverlayTrigger, Button, Form } from 'react-bootstrap'
import { FontAwesomeIcon } from '../../../../Assets/AssetsHelper'
import Productlist from './Components/Productlist'
import AddProductModal from './Components/AddProductModal'
import { SyncProducts } from './Components/SyncProducts'
import SyncProductModal from './Components/SyncProductModal'
import ManualUploadModal from './Components/ManualUploadModal'
import ImportProducts from './Components/ImportProducts'
import { ACTION_PRODUCT_ADD, ACTION_PRODUCT_DELETE, fetchProductList, ACTION_PRODUCT_UPDATE, shopifyProducts, STORE_DELETE } from '../../../../actions/productAction'
import { connect } from 'react-redux'
import { debounce, TOAST_LOADING, TOAST_SUCCESS, TOAST_UPDATE, generateRandomArray } from '../../../../utils'
import Swal from 'sweetalert2'
import { AddMagentoModal } from './Components/AddMagentoModal'
import { Link } from 'react-router-dom'
import { Loader } from '../CommonComponents/Loader'
import withRouter from '../../../routes/WithRouter'
import SearchNotFound from '../CommonComponents/NotFound/SearchNotFound'
import "./product.scss"
import { checkPermission } from '../../../../actions/collaboratorActions'

const ItemCell = (props) => <th className="fw-bold text-nowrap" style={{ minWidth: props.width }}>{props.children}</th>

class ProductCatalog extends Component {
    state = { productsyncmodal: true, currentPage: 1, showPost: 10, showAddProduct: false, searchText: '', paginationItems: [], editProductData: null, selectedProducts: [], manualSyncModal: false, uploadCsv: false, productSync: false, productName: '', addMagento: false, loading: true, m_url: '', searchLoding: false, searching: false }

    componentDidMount() {
        const { currentPage, showPost, searchText } = this.state
        const { allProducts } = this.props;
        this.props.shopifyProducts()
        this.props.fetchProductList(currentPage, showPost, '', searchText, this.generatePagination);

        //for woocommerce
        var url = new URL(window.location.href);
        var success = url.searchParams.get("success");
        if (success) {
            TOAST_SUCCESS('Product Synced Successfully!')
            this.props.navigate('/content/products')
        }

    }

    nextPrev = (no) => event => { this.setState({ currentPage: no }, () => this.props.fetchProductList(no, 10, '', this.state.searchText, this.generatePagination)) }

    searchProduct = debounce(() => {
        const { searchText } = this.state;
        const query = searchText?.length > 1 ? searchText : "";
        this.setState({ loading: true, searchLoding: true, currentPage: 1 }, () =>
            this.props.fetchProductList(1, 10, '', query, this.generatePagination)
        );
    }, 500)

    changePage = (x) => {
        const { showPost, searchText } = this.state
        this.setState({ currentPage: x }, () => { this.props.fetchProductList(x, showPost, '', searchText, this.generatePagination); })
            ;
    }

    generatePagination = () => {
        const { currentPage } = this.state
        const { total_pages } = this.props
        const maxPagesShown = 10;
        let pagination = [];

        if (total_pages <= maxPagesShown) {
            pagination = generateRandomArray(total_pages)
        }
        else {
            if (currentPage <= maxPagesShown / 2) {
                pagination = generateRandomArray(maxPagesShown)
            } else if (currentPage > total_pages - maxPagesShown / 2) {
                pagination = Array.from({ length: maxPagesShown }, (_, i) => total_pages - maxPagesShown + i + 1);
            } else {
                const start = Math.floor(currentPage - maxPagesShown / 2);
                pagination = Array.from({ length: maxPagesShown }, (_, i) => start + i);
            }
        }
        if (pagination[0] !== 1) {
            pagination.unshift(1);
            if (pagination[1] !== 2) {
                pagination.splice(1, 0, '...');
            }
        }
        if (pagination[pagination.length - 1] !== total_pages) {
            if (pagination[pagination.length - 1] !== total_pages - 1) {
                pagination.push('...');
            }
            pagination.push(total_pages);
        }
        this.setState({ loading: false, paginationItems: pagination, searchLoding: false })
    }

    onEditPost = (data) => {
        if(checkPermission('product_catalog', '', true)) this.setState({ editProductData: data, showAddProduct: true })
    }

    onProductSelect = (id, isChecked) => {
        const { selectedProducts } = this.state
        if (isChecked) this.setState({ selectedProducts: [...selectedProducts, id] })
        else this.setState({ selectedProducts: selectedProducts.filter(p => p != id) })
    }

    onAllProductSelect = (isChecked) => {
        if (isChecked) this.setState({ selectedProducts: this.props.allProducts.map(p => p.id) })
        else this.setState({ selectedProducts: [] })
    }

    deleteAllProduct = (product) => {
        const { ACTION_PRODUCT_DELETE } = this.props
        const { selectedProducts, paginationItems } = this.state
        if(checkPermission('product_catalog', '', true)){
            Swal.fire({
                title: 'Are you sure ?',
                text: `Are you sure you want to permanently delete selected ${this.state.selectedProducts.length > 1 ? 'products' : 'product'}?`,
                icon: 'warning',
                showCancelButton: true,
                confirmButtonText: 'Yes, delete it!'
            }).then((result) => {
                if (result.value) {
                    const toastId = TOAST_LOADING()
                    ACTION_PRODUCT_DELETE({ id: product ? [product.id] : selectedProducts }, () => {
                        if (paginationItems.length > 1) {
                            this.setState({ loading: true }, () => this.refreshProducts())
                        }
                        TOAST_UPDATE(toastId, "success", "Successfully Deleted !")
                    })
                    this.onAllProductSelect()
                }
                else if (result.dismiss === "cancel") TOAST_SUCCESS(`Your ${this.state.selectedProducts.length > 1 ? 'products are' : 'product is'} safe.`)
            })
        }
    }

    refreshProducts = (type) => {
        const { searchText } = this.state
        this.setState({ productSync: false })
        this.props.fetchProductList(1, 10, '', type ? searchText : '', this.generatePagination)
        this.props.shopifyProducts()
    }

    onDelete = (id) => {
        if(checkPermission('product_catalog', '', true)){
            Swal.fire({
                icon: "warning",
                title: "Are You Sure?",
                html: `<p>Are you sure you want to delete the product catalog from Tagshop?</p><p class="text-danger fs-10 mb-0">Attention! Tagged products from this catalog will also be removed.</p>`,
                showCancelButton: true,
                cancelButtonText: "Cancel",
                confirmButtonText: "Delete",
            }).then((result) => {
                if (result.value) {
                    this.props.STORE_DELETE(id, this.refreshProducts)
                }
                else if (result.dismiss === "cancel") {
                    TOAST_SUCCESS('Your product is safe.')
                }
            });
        }
    }

    render() {
        const { currentPage, addMagento, showAddProduct, searchText, paginationItems, editProductData, selectedProducts, manualSyncModal, uploadCsv, productSync, productName, loading, m_url, searchLoding, searching } = this.state
        const { allProducts, stores, loadingProd_cat, onBoardings } = this.props;
        return <div className='d-flex flex-column flex-column-fluid w-100 py-5 products_'>
            <Container fluid>
                {((stores?.length || allProducts?.length || searching || localStorage.getItem('shopifyLogin')) && !onBoardings) ?
                    <Card className='border-none'>
                        <Card.Header className='f-between pt-lg-6 pt-4'>
                            <Button id="import_pro" variant='link' className='mb-3 mb-lg-0 f-center text-nowrap' onClick={event => checkPermission('product_catalog', '', true) ? this.setState({ uploadCsv: true }) :{}}>
                                <FontAwesomeIcon type={'solid'} icon={'download'} />
                                Import Product List
                            </Button>
                            <div className="d-flex justify-content-end flex-wrap flex-sm-row flex-column flex-grow-1">
                                <Button variant='dark' size="sm" className="me-sm-2 mb-2 text-nowrap" onClick={event => checkPermission('product_catalog', '', true) ? this.setState({ productSync: true }) : {}}>Sync Product Catalog</Button>
                                <Link id="tag_pro" to={`/content/moderation/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') : ''}`} className="btn btn-primary btn-sm me-sm-2 mb-2 text-nowrap">
                                    <FontAwesomeIcon type={'solid'} icon={'tag'} />
                                    Tag Products
                                </Link>

                                <Button id="add_pro" variant='primary' size="sm" className="mb-2 text-nowrap" onClick={event => checkPermission('product_catalog', '', true) ? this.setState({ showAddProduct: true }) : {}}>
                                    <FontAwesomeIcon type={'solid'} icon={'plus'} />
                                    Add Product
                                </Button>
                            </div>
                        </Card.Header>
                        <Card.Body className='py-0 pe-0'>
                            <div className="d-flex align-items-start justify-content-between flex-wrap pt-3">
                                <div className="f-between w-100">
                                    <div className="p-0 position-relative w-100 mb-2 d-flex flex-grow-1" style={{ maxWidth: 320 }}>
                                        <span className={`p-0 w-100 ${searchLoding ? 'spinner spinner-gray spinner-sm' : ''}`}>
                                            <Form.Control id="search_pro" type='text' className='pe-8' value={searchText} onChange={(event) => this.setState({ searchText: event.target.value, searching: true }, () => this.searchProduct())} placeholder="Search by product name, SKU, or URL" />
                                            {!searchLoding ?
                                                <span id="search_spinner" className="h-38px w-38px text-gray-500 f-center position-absolute top-50 translate-middle fs-6" style={{ right: -19 }}
                                                    onClick={() => searchText ? this.setState({ searchText: '' }, () => this.searchProduct()) : []}
                                                >
                                                    <FontAwesomeIcon icon={searchText ? "xmark" : 'magnifying-glass'} />
                                                </span> :
                                                null
                                            }
                                        </span>
                                    </div>
                                    {selectedProducts && allProducts && (selectedProducts.length > 1 || selectedProducts.length == allProducts.length) && allProducts.length != 0 ? <Button id="d_selected_btn" variant='danger' size="sm" className="mb-2 me-4" onClick={() => this.deleteAllProduct()}>
                                        <FontAwesomeIcon icon={'trash-can'} />
                                        Delete {selectedProducts.length == allProducts.length ? 'All' : `(${selectedProducts.length})`}
                                    </Button> : null}
                                </div>
                            </div>
                            <Table className='mb-0 pb-3' size='lg' responsive>
                                <thead className="thead-light">
                                    <tr>
                                        <th className="fw-bold">
                                            <div className="form-check">
                                                <Form.Check id="check_all_pro" checked={selectedProducts.length != 0 && selectedProducts.length == allProducts.length} onChange={(e) => this.onAllProductSelect(e.target.checked)} />
                                            </div>
                                        </th>
                                        <ItemCell width={200}>Product</ItemCell>
                                        <ItemCell width={50}>Price</ItemCell>
                                        <ItemCell width={50}>Discount <OverlayTrigger placement='top' overlay={<Tooltip>This represents the price after subtracting the discount.</Tooltip>}>
                                            <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'fs-5 ms-1 text-gray-500'} /></span>
                                        </OverlayTrigger></ItemCell>
                                        <ItemCell width={200}>Product URL</ItemCell>
                                        <ItemCell width={50}>Product Id</ItemCell>
                                        <ItemCell width={50}>Category</ItemCell>
                                        <ItemCell width={50}>SKU</ItemCell>
                                        <ItemCell width={50}>Tags</ItemCell>
                                        <th className="fw-bold text-nowrap text-end position-sticky end-0 pe-4" style={{ minWidth: 100 }}>Actions</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {allProducts?.length > 0 && !loading ? allProducts.map((item, index) => <Productlist key={index} product={item} selectedProducts={selectedProducts} onProductSelect={this.onProductSelect} onEditPost={this.onEditPost} onDelete={this.deleteAllProduct} />) :
                                        <tr>
                                            <td className='border-0' colSpan="10" style={{ textAlign: 'center' }}>
                                                {loading ? <Loader bg="body" /> : <SearchNotFound title="No Product Found" desc="We can't find any item matching your search." icon="product" height={'calc(100vh - 400px)'} />}

                                            </td>
                                        </tr>
                                    }
                                </tbody>
                            </Table>

                            {allProducts?.length > 0 && paginationItems?.length > 1 && !loading ?
                                <ul className="pagination pagination-sm mb-10 mt-5">
                                    <li className="page-item">
                                        <span className={`page-link cursor-pointer ${currentPage == 1 ? ' disabled' : ' cursor-pointer'}`} onClick={this.nextPrev(currentPage - 1)}>
                                            <FontAwesomeIcon icon={'angle-left'} />
                                        </span>
                                    </li>
                                    {paginationItems.map((x, i) => <li key={i} id={`p_num_${x}`} className={`page-item${currentPage == x ? " active" : ' cursor-pointer'} ${x == '...' ? ' disabled' : ''}`} onClick={() => this.changePage(x)}><span className="page-link">{x}</span></li>)}

                                    <li className="page-item">
                                        <span className={`page-link cursor-pointer ${currentPage == this.props.total_pages ? ' disabled' : ' cursor-pointer'}`} onClick={this.nextPrev(currentPage + 1)}>
                                            <FontAwesomeIcon icon={'angle-right'} />
                                        </span>
                                    </li>
                                </ul> : null}
                        </Card.Body>
                    </Card>
                    :
                    loadingProd_cat && !loading ?
                        <SyncProducts openManualSyncModal={() => this.setState({ manualSyncModal: true })} openSyncProduct={value => this.setState({ productName: value, productSync: true })} />
                        :
                        onBoardings? <SyncProducts openManualSyncModal={() => this.setState({ manualSyncModal: true })} openSyncProduct={value => this.setState({ productName: value, productSync: true })} /> : <Loader bg={'body'} />
                }
            </Container>

            {productSync ? <SyncProductModal show={productSync} refreshProducts={this.refreshProducts} onDelete={this.onDelete} onHide={() => this.setState({ productSync: false, productName: '' })} product={productName} stores={stores} addMagento={addMagento} showMagento={(url) => this.setState({ addMagento: true, productSync: false, m_url: url })} /> : null}

            <ImportProducts show={uploadCsv} onHide={() => this.setState({ uploadCsv: false })} countAllPage={this.refreshProducts} />

            {showAddProduct ? <AddProductModal showAddProduct={showAddProduct} editProductData={editProductData} ACTION_PRODUCT_UPDATE={this.props.ACTION_PRODUCT_UPDATE} closePopup={() => this.setState({ showAddProduct: false, editProductData: null })} ACTION_PRODUCT_ADD={this.props.ACTION_PRODUCT_ADD} /> : null}

            <ManualUploadModal show={manualSyncModal} handleClose={() => this.setState({ manualSyncModal: false })} uploadCsv={() => this.setState({ uploadCsv: true, manualSyncModal: false })} onAddProduct={() => this.setState({ showAddProduct: true, manualSyncModal: false })} />
            {addMagento && <AddMagentoModal show={addMagento} onHide={() => this.setState({ addMagento: false })} refreshProducts={this.refreshProducts} m_url={m_url} />}

        </div>
    }
}



const mapStateToProps = state => {
    return {
        allProducts: state.products?.allProducts,
        total_pages: state.products?.total_pages,
        loadingProd_cat: state.products?.loadingProd_cat,
        stores: state.products?.connectedStores,
        data: state.products.loadingProd_cat,
        dataNew: state.products,
    }
}
const mapDispatchToProps = dispatch => {
    return {
        fetchProductList: (page, showPost, tag, search, callBack) => dispatch(fetchProductList(page, showPost, tag, search, callBack)),
        ACTION_PRODUCT_ADD: (obj, callBack) => dispatch(ACTION_PRODUCT_ADD(obj, callBack)),
        ACTION_PRODUCT_UPDATE: (obj, callBack) => dispatch(ACTION_PRODUCT_UPDATE(obj, callBack)),
        ACTION_PRODUCT_DELETE: (obj, callBack) => dispatch(ACTION_PRODUCT_DELETE(obj, callBack)),
        shopifyProducts: () => dispatch(shopifyProducts()),
        STORE_DELETE: (id, callback) => dispatch(STORE_DELETE(id, callback))
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(withRouter(React.memo(ProductCatalog)))