import React, { Component } from 'react'
import Swal from 'sweetalert2'
import { Form, Tooltip, OverlayTrigger, Button, Badge } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper'
import { onDeletePop, TOAST_ERROR, TOAST_LOADING } from '../../../../../utils'


const ActionButton = ({ toolTip, clickAction, product, iconImg }) => {
    return <OverlayTrigger placement="top" overlay={<Tooltip>{toolTip}</Tooltip>}>
        <Button id={`action-${iconImg}`} variant="icon" size='sm' className={`btn-active-${iconImg =='pen' ? 'primary' :'danger'} ms-2`} onClick={event => clickAction(product)}>
            <FontAwesomeIcon type="solid" icon={iconImg}/>
        </Button>
    </OverlayTrigger>
}
const span = ({ children }) => <span className="text-gray-700 text-nowrap">{children}</span>
class Productlist extends Component {

    onErrorImageUpdate = event => event.target.src = baseUrl(`media/images/error/no-product.svg`)

    render() {
        const { product, onEditPost, onProductSelect, selectedProducts, onDelete } = this.props
        const tags = product.tags?product.tags.split(", "):[];
        return <tr>
            <td>
                <div className="form-check">
                    <Form.Check id={`check-${product?.id}`} checked={selectedProducts.includes(product?.id)} onChange={event => onProductSelect(product?.id, event.target.checked)} />
                </div>
            </td>
            <td>
                <div className="d-flex align-items-center mr-2">
                    <div className="symbol symbol-50 rounded-2 flex-shrink-0 me-3 overflow-hidden">
                        <img data-media={`product-${product?.id}`} className="object-center-contain" src={product.product_image} height={50} width={50} alt={product.product_title} onError={this.onErrorImageUpdate} loading='lazy'/>
                    </div>
                    <div className="overflow-hidden">
                        <span data-title={`product-${product?.id}`}  className="d-block fs-8 fw-semibold text-ellipsis" style={{ maxWidth: 200 }}>{product.product_title}</span>
                    </div>
                </div>
            </td>
            <td>
                <span data-price={`price-${product?.id}`}>{product.product_price ? `${product.price_currency_symbol}${product.product_price}` : '--'}</span>
            </td>
            <td>
                <span data-price={`discount-price-${product?.id}`}>{product?.product_discount > 0 ? `${product.price_currency_symbol}${product.product_discount}` : '--'}</span>
            </td>
            <td>
                <div className="position-relative">
                    <Form.Control id={`link-${product?.id}`} size='sm' className="pe-7 text-gray-600" value={product.product_url} readOnly />
                    <Link id={`link-open-${product?.id}`} className="btn btn-link position-absolute top-50 translate-middle" to={product.product_url} target="_blank" style={{right:-5}}>
                        <FontAwesomeIcon icon={'external-link'}/>
                    </Link>
                </div>
            </td>
            <td>
                <div className="w-auto">
                    {product.shopify_product_id?.length > 0 ? <Form.Control id={`pro-id-${product?.id}`} size='sm' className="pe-4 text-gray-600" value={product.shopify_product_id} style={{ maxWidth: 150, minWidth:100 }} readOnly /> : '--'}
                </div>
            </td>
            <td>
                <span id={`cat-${product?.id}`} className='d-inline-block break-all' style={{overflowWrap:'anywhere',maxWidth: 200}}>{product.shopify_product_type ? product.shopify_product_type : '--'}</span>
            </td>
            <td>
                <span id={`sku-${product?.id}`}>{product.product_sku ? product.product_sku : '--'}</span>
            </td>
            <td style={{minWidth:200}}>
                <div className='' style={{maxWidth:200}}>
                {tags.length > 0 ? tags.map((Item, index) =>
                    <Badge data-tags={`tag-${product?.id}-${index}`} key={index} bg='' className='badge-secondary'>{Item}</Badge>
                )
                : ' -- '
                }
                </div>
            </td>
            <td className="text-end position-sticky end-0 pe-4">
                <div className="d-flex align-items-center justify-content-end">
                    <ActionButton toolTip="Edit" clickAction={onEditPost} product={product} iconImg="pen" />
                    <ActionButton toolTip="Remove" clickAction={onDelete} product={product} iconImg="trash-can" />
                </div>
            </td>
        </tr>
    }
}

export default Productlist