import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { Modal, Button, Form, Alert } from 'react-bootstrap'
import Select from 'react-select'
import { MAGENTO_TOKEN_VERIFY, SYNC_MAGENTO_PRODUCTS } from '../../../../../actions/productAction'
import { TOAST_ERROR } from '../../../../../utils'

const regex = /^(http:\/\/|https:\/\/).+/;
export const AddMagentoModal = ({ show, onHide, refreshProducts, m_url }) => {
    const [storeList, setStoreList] = useState([])
    const [m_token_spinner, set_m_token_spinner] = useState(false)
    const [m_token, setM_token] = useState('')
    const [m_stores, setM_stores] = useState(null)
    const [selectedStore, setSelectedStore] = useState('')
    const [syncSpinner, setSyncspinner] = useState(false)
    const [error, setError] = useState('')

    const onVerifyToken = () => {
        var url = m_url
        
        set_m_token_spinner(true)
        if (!regex.test(url)) {
            url = `https://${url}`
        }
        MAGENTO_TOKEN_VERIFY({ accessToken: m_token, magentourl: url }).then(res => {
            const { status, storelist, message } = res.data
            
            if (status && Array.isArray(storelist)) {
                let storeLiting = storelist.map(item => {return {label:item.name, value:item.code}})
                setStoreList(storeLiting)
                setM_stores(storelist)
                set_m_token_spinner(false)
                if(storelist.length) setSelectedStore(storeLiting[0])
            }
            else{
                TOAST_ERROR(Array.isArray(storelist) ? message : storelist.message)
                set_m_token_spinner(false)
            }
        })
    }
    const onSyncProduct = () => {
        setSyncspinner(true)
        SYNC_MAGENTO_PRODUCTS(selectedStore.value, m_token, !regex.test(m_url) ? `https://${m_url}` : m_url, onClose)
    }
    const setbacksyncspinner=()=> setSyncspinner(true)
    
    const onClose = (message) => {
        setSyncspinner(false)
        setError(message)
        if(!message){
             onHide()
             refreshProducts()
        } 
    }
    return (
        <Modal show={show} onHide={onHide} size="md" centered>
            <Modal.Header closeButton>
                <Modal.Title>Connect Your Store</ Modal.Title>
            </Modal.Header>
            <Modal.Body className='p-5'>
                <p className='h5'>You are trying to connect the Store. Please Enter Access Token for your website.</p>
                <Form.Group className="mb-3">
                    <Form.Label>Access Token</Form.Label>
                    <div className='position-relative'>
                        <span className={`p-0 w-100 ${m_token_spinner?'spinner':''} spinner-gray`}>
                            <Form.Control size='lg' className={`fw-bold ${error ? 'is-invalid' : ''}`} placeholder='Enter Access Token' value={m_token} onChange={e => setM_token(e.target.value)} id="accessToken" required />
                            <span className='invalid-feedback'>{error}</span>
                        </span>
                    </div>
                </Form.Group>
                {storeList && storeList.length > 0 ? <Form.Group className="mb-3">
                    <Form.Label>Choose Store</Form.Label>
                    <Select className={`selectpicker selectpicker-lg`} classNamePrefix="selectpicker" options={storeList} value={selectedStore} onChange={e => setSelectedStore(e)} /> 
                </Form.Group>: null}
                
                <Alert variant='warning' className='rounded-1 p-2'>Note: Only Compatible with Magento 2.x</Alert>

                <p className="mb-3">How to generate access Token. <Link className="btn btn-link p-0" to="https://devdocs.magento.com/guides/v2.4/get-started/authentication/gs-authentication-token.html" alt="How to generate access Token." target="_blank">Click here</Link> </p>
                
            </Modal.Body>
            <Modal.Footer>
                <div className="d-flex align-items-center justify-content-end w-100">
                    <Button variant="secondary" className='me-2' onClick={()=>onClose()}>Close</Button>
                    {!m_stores ? <Button variant='primary' id="verify_tok_btn" onClick={onVerifyToken}>Verify Token</Button>
                        : <Button variant='primary' id="sync_pro_btn_" className={`${syncSpinner ? 'spinner' :''}`} onClick={onSyncProduct}>Sync Product</Button>}
                </div>
            </Modal.Footer>
        </Modal>
    )
}
