import React, { Component } from 'react'
import { Modal, Row, Col, Form, OverlayTrigger, Tooltip, Button, Image } from 'react-bootstrap';
import Select from 'react-select';
import { DashboardModal } from "@uppy/react";
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { Link } from 'react-router-dom';
import $ from 'jquery'
import UppyUpload from '../../../../../utils/uppyUpload';
import { validURL } from '../../../../../utils'
import { currencyData } from './Currency'
import { TEMP_URL } from '../../../../../actions/constants';

export default class AddProductModal extends Component {
    uppyUploader = new UppyUpload(null, 5000000)
    state = { pName: '', pPrice: '', dPrice: '', pUrl: '', sku: '', pId: '', category: '', tags: '', filename: '', selectedFile: '', selected: '', modalOpen: false, errorShow: false, preview: false, pCurrency: 'USD', pCurrencySymbol: '$', isLoading: false, currencySelect: currencyData[2], pNameError: false, pPriceError: false, pUrlError: false }

    componentDidMount() {
        const { editProductData } = this.props
        if (editProductData) {
            /*
            
            if (event && Object.keys(event).length > 0 && event.value) {
            let currency = event.value.split(',')
            this.setState({ currencySelect: event, pCurrency: currency[1], pCurrencySymbol: currency[0] })
        }
            */
            const currencyUpdate = currencyData.filter(currency => currency.label == editProductData.price_currency)
            let pCurrency = ""; let pCurrencySymbol = "";
            if (currencyUpdate?.length > 0 && currencyUpdate[0]?.value) {
                let currencyConvert = currencyUpdate[0].value.split(',')
                pCurrency = currencyConvert[1]; pCurrencySymbol = currencyConvert[0];
                this.setState({
                    pName: editProductData.product_title, pPrice: editProductData.product_price, dPrice: editProductData.product_discount, pUrl: editProductData.product_url, sku: editProductData.product_sku, pId: editProductData.shopify_product_id, category: editProductData.shopify_product_type, tags: editProductData.tags, filename: editProductData.product_image, selectedFile: editProductData.product_image, selected: editProductData.product_image, pCurrency: pCurrency, pCurrencySymbol: pCurrencySymbol, currencySelect: currencyData.filter(currency => currency.label == editProductData.price_currency)
                })
            }

        }

        this.uppyUploader.uppy.on('transloadit:result', (stepName, result) => {
            this.setState({ selectedFile: result.id, filename: `${TEMP_URL}${result.id}.${result.ext}`, selected: `${TEMP_URL}${result.id}.${result.ext}` }, () => {
                setTimeout(() => { this.uppyUploader.uppy.cancelAll(); this.setState({ modalOpen: false }) }, 300)
            })
        })
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.editProductData && nextProps.editProductData !== this.props.editProductData) {
            const { editProductData } = nextProps
            this.setState({
                pName: editProductData.product_title, pPrice: editProductData.product_price, dPrice: editProductData.product_discount, pUrl: editProductData.product_url, sku: editProductData.product_sku, pId: editProductData.shopify_product_id, category: editProductData.shopify_product_type, tags: editProductData.tags, filename: editProductData.product_image, selectedFile: editProductData.product_image, selected: editProductData.product_image, pCurrency: editProductData.discount_currency, pCurrencySymbol: editProductData.discount_currency_symbol
            })
        }

    }

    onClickToOpen = event => {
        this.setState({ modalOpen: true })
        $(".uppy-Dashboard-close").click(() => {
            this.uppyUploader.uppy.cancelAll()
            this.setState({ modalOpen: false })
        })

    }

    onClickToClose = () => {
        this.uppyUploader.uppy.cancelAll()
        this.setState({ modalOpen: false })
    }

    componentWillUnmount() {
        this.uppyUploader.uppy.cancelAll()
        this.uppyUploader.uppy.close({ reason: 'unmount' })
        this.setState({ modalOpen: false })
    }

    doneButtonHandler = () => {
        this.uppyUploader.uppy.cancelAll()
        this.onClickToClose()
    }

    submitForm = event => {
        const { pName, pPrice, dPrice, pUrl } = this.state;
        this.setState({ isLoading: true });
        const aPrice = parseInt(pPrice) ? parseInt(pPrice) : 0
        const discPrice = parseInt(dPrice) ? parseInt(dPrice) : 0
        if (!pName || !validURL(pUrl) || aPrice < discPrice) {
            this.setState({ pNameError: !pName ? true : false, pUrlError: !validURL(pUrl) ? true : false, pPriceError: aPrice < discPrice ? true : false, isLoading: false })
        }
        else this.onProductAdd();
    }

    onCurrenyChange = event => {
        if (event && Object.keys(event).length > 0 && event.value) {
            let currency = event.value.split(',')
            this.setState({ currencySelect: event, pCurrency: currency[1], pCurrencySymbol: currency[0] })
        }
    }

    onProductAdd = () => {

        const { filename, selectedFile, image, file, pName, pPrice, dPrice, pUrl, sku, pId, category, tags, preview, pCurrency, pCurrencySymbol } = this.state
        const { ACTION_PRODUCT_ADD, closePopup, editProductData, ACTION_PRODUCT_UPDATE } = this.props

        var productData = {
            "wall": localStorage.getItem("wallId"),
            "image": filename,
            "title": pName,
            "priceCurrency": pCurrency,
            "priceCurrencySymbol": pCurrencySymbol,
            "price": pPrice,
            "discountCurrencySymbol": pCurrencySymbol,
            "discountCurrency": pCurrency,
            "discount": dPrice,
            "url": pUrl,
            "sku": sku,
            "shopify_product_id": pId,
            "shopify_product_type": category,
            "shopify_product_tags": tags
        }

        if (editProductData && editProductData.id) {
            productData['id'] = editProductData.id
            ACTION_PRODUCT_UPDATE(productData, this.resetCallback)
        }
        else ACTION_PRODUCT_ADD(productData, this.resetCallback)

    }

    resetCallback = () => this.setState({ isLoading: false }, () => this.props.closePopup());

    onClickRemove = event => this.setState({ filename: '', selectedFile: '', selected: '' })

    render() {
        const { showAddProduct, closePopup, editProductData } = this.props
        const { filename, modalOpen, pName, pPrice, dPrice, pUrl, sku, pId, category, tags, preview, pCurrencySymbol, isLoading, currencySelect, pNameError, pPriceError, pUrlError } = this.state


        return (
            <Modal show={showAddProduct} size="lg" dialogClassName="modal-dialog-centered" id="addNewCustomProduct" onHide={closePopup}>
                <Modal.Header closeButton>
                    <Modal.Title> {editProductData?.id ? `Update` : `Add`} Product</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div className="addPro_modal mx-xl-4 px-0 px-lg-4 mx-0">
                        <Row>
                            <Col md={6} lg={5}>
                                <p className='text-center fw-medium text-gray-400 fs-8 mb-1'>Product {preview ? 'Preview' : 'Image'}</p>
                                <div className="border rounded-1 mb-4 mb-lg-0 position-relative overflow-hidden" style={{ minHeight: 430 }}>
                                    {preview ?
                                        <div className="tb_pro__wrap_in p-5 mt-6">
                                            <div className="tb_pro__img_wrapper">
                                                <div className="tb_pro__image_wrap mediaHolder">
                                                    <img className="tb_pro__image" src={filename ? filename : baseUrl('media/images/error/no-product.svg')} />
                                                </div>
                                            </div>
                                            <div className="tb_pro__title">{pName}</div>
                                            <div className="tb_pro__price_wrap">
                                                {dPrice == 0 ? <div className="tb_pro__price">{pCurrencySymbol}{pPrice ? pPrice : 0}</div>
                                                    : <>
                                                        <div className="tb_pro__price">{pCurrencySymbol}{dPrice ? dPrice : 0}</div>
                                                        <div className="tb_pro__discount_price">{pCurrencySymbol}{pPrice ? pPrice : 0}</div>
                                                    </>}

                                            </div>
                                            <div className="tb_pro__link">
                                                <Link className="tb_pro__btn text-white" to={pUrl} target="_blank">Shop Now</Link>
                                            </div>
                                        </div>
                                        :

                                        filename ?
                                            <div className='p-3 mb-2'>
                                                <Image id="product_img" className='w-100 mb-10' fluid src={filename} />
                                                <div className='f-center position-absolute bottom-0 start-0 w-100 p-2'>
                                                    <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">Edit</Tooltip>}>
                                                        <Button id="product_edit" variant="icon" className="btn-active-primary mx-1" onClick={this.onClickToOpen}>
                                                            <FontAwesomeIcon icon={'pen'} />
                                                        </Button>
                                                    </OverlayTrigger>
                                                    <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">Remove</Tooltip>}>
                                                        <Button id="product_remove" variant="icon" className="btn-active-primary mx-1" onClick={this.onClickRemove}>
                                                            <FontAwesomeIcon icon={'trash-can'} />
                                                        </Button>
                                                    </OverlayTrigger>
                                                </div>
                                            </div>
                                            :
                                            <div id="upload_pro_media" className="f-center flex-column cursor-pointer" onClick={this.onClickToOpen} style={{ height: 395 }}>
                                                <span className='text-gray-300 mb-2' style={{ fontSize: 80 }}>
                                                    <i className="fa-duotone fa-solid fa-bag-shopping-plus"></i>
                                                </span>
                                                <strong className="text-center text-muted mb-2">Click here</strong>
                                                <span>to upload the new image</span>
                                            </div>
                                    }
                                </div>
                            </Col>
                            <Col md={6} lg={7}>
                                <div className="mb-2 mb-xl-3">
                                    <Form.Label>Product Name</Form.Label>
                                    <Form.Control id="pro_name" name="pname" className={pNameError ? 'is-invalid' : ''} value={pName} placeholder="Enter Product Name" onChange={(e) => this.setState({ pName: e.target.value })} />
                                    {pNameError && <div className="invalid-feedback d-block">Product name is required.</div>}
                                </div>
                                <div className="row mb-2 mb-xl-3">
                                    <div className="col">
                                        <Form.Label>Price</Form.Label>
                                        <div className="input-group">
                                            <span className="input-group-text p-0">
                                                <Select id="select_currency" className={`selectpicker selectpicker-sm border-0`} classNamePrefix="selectpicker" onChange={this.onCurrenyChange} name="Price" value={currencySelect} options={currencyData} />
                                            </span>
                                            <Form.Control id="input_price" value={pPrice} className={pPriceError ? 'is-invalid' : ''} placeholder="0.00" size="8" type="number" min={0} onChange={(e) => this.setState({ pPrice: e.target.value })} />
                                        </div>
                                    </div>
                                    <div className="col">
                                        <Form.Label>Discounted Price
                                            <OverlayTrigger placement='top' overlay={<Tooltip>This represents the price after subtracting the discount.</Tooltip>} >
                                                <sapn className="ms-1">
                                                    <FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'fs-7 text-gray-500'} />
                                                </sapn>
                                            </OverlayTrigger>
                                        </Form.Label>
                                        <div className="input-group">
                                            <span className="input-group-text">{pCurrencySymbol}</span>
                                            <Form.Control id="input_discount" value={dPrice} placeholder="0.00" size="8" type="number" min={0} onChange={(e) => this.setState({ dPrice: e.target.value })} className={pPriceError ? 'is-invalid' : ''} />
                                            {pPriceError && <div className="invalid-feedback d-block">Discounted price should be less than actual price.</div>}
                                        </div>
                                    </div>
                                </div>
                                <div className="mb-2 mb-xl-3">
                                    <Form.Label>Product URL</Form.Label>
                                    <Form.Control id="input_url" value={pUrl} className={pUrlError ? 'is-invalid' : ''} placeholder="https://www.website.com" onChange={(e) => this.setState({ pUrl: e.target.value })} />
                                    {pUrlError && <div className="invalid-feedback d-block">Please enter valid url.</div>}
                                </div>
                                <div className="row mb-2 mb-xl-3">
                                    <div className="col">
                                        <Form.Label>Product SKU</Form.Label>
                                        <Form.Control id="input_sku" value={sku} placeholder="Enter Product SKU" onChange={(e) => this.setState({ sku: e.target.value })} />
                                    </div>
                                    <div className="col">
                                        <Form.Label>Product Id</Form.Label>
                                        <Form.Control id="input_p_id" value={pId} placeholder="Enter Product Id" onChange={(e) => this.setState({ pId: e.target.value })} />
                                    </div>
                                </div>
                                <div className="mb-2 mb-xl-3">
                                    <Form.Label>Category</Form.Label>
                                    <Form.Control id="input_p_cat" value={category} placeholder="Enter Category Id" onChange={(e) => this.setState({ category: e.target.value })} />
                                </div>
                                <div className="mb-2 mb-xl-3">
                                    <Form.Label>Tags</Form.Label>
                                    <Form.Control id="input_p_tag" value={tags} placeholder="Enter Multiple Tags Comma Separated" onChange={(e) => this.setState({ tags: e.target.value })} />
                                </div>
                            </Col>
                        </Row>
                    </div>

                </Modal.Body>
                <Modal.Footer className="p-3">
                    <Button id="btn_preview" variant='outline-primary' size='sm' className="ms-2" onClick={() => this.setState({ preview: !preview })}>{!preview ? `Preview` : `Edit Mode`}</Button>
                    <Button id="product_save_" variant='primary' size='sm' className={`ms-2 ${isLoading ? 'spinner' : ''}`} onClick={this.submitForm}>Submit</Button>
                </Modal.Footer>
                <DashboardModal
                    animateOpenClose={true}
                    proudlyDisplayPoweredByUppy={false}
                    inline={false}
                    showProgressDetails={true}
                    height={750}
                    browserBackButtonClose={true}
                    showRemoveButtonAfterComplete={false}
                    uppy={this.uppyUploader.uppy}
                    open={modalOpen}
                    onRequestCloseModal={this.onClickToClose}
                    closeAfterFinish={true}
                    doneButtonHandler={this.doneButtonHandler}
                    plugins={[
                        "Instagram",
                        "GoogleDrive",
                        "Url",
                        "Webcam"
                    ]}
                />
            </Modal>

        )
    }
}